















import { Vue, Component, Prop } from "vue-property-decorator";

@Component({
  components: {},
})
export default class SelectNumber extends Vue {
  @Prop() label = " ";
  @Prop() unit = " ";
  value = 5;

  mounted(): void {
    this.change();
  }

  change(): void {
    console.log("select number - change");
    this.$emit("changed", this.value);
  }
}
